/* ---------------------------------- */
/* owl carousel padding */
/* ---------------------------------- */

.owl-carousel .owl-item {
    padding: 3px;
}


/* ---------------------------------- */
/* duvidas */
/* ---------------------------------- */

/* label focus color */
.input-field input:focus+label {
    color: #7b1fa2 !important;
}

/* label underline focus color */
.row .input-field input:focus {
    border-bottom: 1px solid #7b1fa2 !important;
    box-shadow: 0 1px 0 0 #7b1fa2 !important;
}


/* ---------------------------------- */
/* nossos cursos (carousel) */
/* ---------------------------------- */

/* definir a cor dos links dos cards no carousel (era laranja) */
.card a {
    color: #7b1fa2 !important;
}

.card a:hover {
    color: #ab47bc !important;
}


/* ---------------------------------- */
/* Sobre a lemes artes */
/* ---------------------------------- */

/* arrumar background do collapsible (antes ficava com a cor background section */
.collapsible {
    background-color: white;
}



/* ---------------------------------- */
/* parallax lemes artes em números */
/* ---------------------------------- */


.parallax {
    width: 100%;
    background-image: url(../img/parallax.jpg);
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
}

.parallax .container,
.parallax .row {
    /* height: 100%; */
    /* min-height: 500px; */
}

.parallax .col-md-4 {
    padding-right: 2px;
    padding-left: 2px;
}

/* cover */
.parallax-cover {
    width: 100%;
    /* min-height: 500px; */
    height: 100%;
    background: #00000088;
    padding: 50px 0;
}

/* parallax item */
.parallax-item {
    margin-top: 25px;
    border: 1px solid white;
    text-align: center;
    padding: 25px 0px;
    background-color: rgba(131, 131, 131, 0.712);
}

.parallax-item i {
    font-size: 45px;
    margin-bottom: 30px;
}

.parallax-item h5 {
    font-weight: 900;
    font-size: 33px;
    margin-bottom: 30px;
}

.parallax-item p {
    color: rgb(219, 219, 219);
}




/* ---------------------------------- */
/* Por que lemes artes? */
/* ---------------------------------- */

.vantagens .col {
    box-sizing: border-box;
}



/* ---------------------------------- */
/* depoimentos */
/* ---------------------------------- */

/* img */
.depoimento-img img {
    border-radius: 100% !important;
    width: 150px !important;
    margin-top: 30px;
    border: 5px solid #ffffff;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
}

/* texto */
.depoimento p {
    font-style: italic;
    text-align: justify;
}

/* titulo */
.depoimento h5 {
    font-weight: 700;
}
.depoimento h6 {
    font-weight: 400;
    font-style: italic;
}

/* redes sociais */
.depoimento ul {
    margin-top: 30px;
    margin-bottom: 0px;
}

.depoimento ul a {
    font-size: 30px;
    color: rgb(255, 255, 255) !important;
    margin: 0px 5px;
}
.depoimento ul a:hover {
    color: rgb(201, 201, 201) !important;
}