@import './header.css';
@import './content.css';
@import './footer.css';

/* configurar o sass depois */
/* primary color : #1e88e5 */
/* secondary color : #7b1fa2 */

/* ---------------------------------- */
/* materialize config */
/* ---------------------------------- */
.icon-block {
    padding: 0 15px;
}

.icon-block .material-icons {
    font-size: inherit;
}

.dropdown-content li>a {
    color: #1e88e5;
}

.dropdown-content li>span {
    color: #7b1fa2;
}


/* ---------------------------------- */
/* classes */
/* ---------------------------------- */
.gray-bg {
    background-color: #eeeeee;
}

.word-break-break {
    word-break: break-all;
}
