/* bug fix */
.navbar-fixed {
  z-index: 9999;
  position: absolute;
}


/* NAVBAR */


/* nav color */
header nav {
  background-color: transparent !important;
  box-shadow: none;
}

/* muda a cor da nav ao descer */
.nav-down {
  background-color: #2196f3 !important;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
}

/* padding */
header nav {
  padding-bottom: 30px;
}

/* ajusta o logo */
.brand-logo {
  line-height: 80px;
}

@media only screen and (max-width: 600px) {
  .brand-logo {
    line-height: 70px;
  }

}

/* aumenta a width dos dropdowns */
.dropdown-content {
  width: 250px !important;
}

/* dropdown redes sociais icons size */
nav .dropdown-content i {
  font-size: 40px;
  color: #2196f3 !important;
}

/*  redes sociais dropmenu config */
.dropdown-item span {
  position: relative;
  top: -10px;
  left: +3px;
}











/* carousel */

.mask {
  top: 0px;
  position: absolute;
  width: 100%;
  height: 100vh;
  background-color: #2196f3ea !important;
}



/* config */
.carousel-img-config {
  width: 100%;
  height: 100vh;
  /* min-height: 720px; */
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  backface-visibility: hidden;
  background-image: url();
}

.carousel-zoom-animation {
  animation-name: carouselZoom;
  animation-duration: 15s;
  animation-fill-mode: forwards;
  /* animation-fill-mode: initial; */
  animation-iteration-count: infinite;
}

@keyframes carouselZoom {
  from {
    transform: scale(1);
  }

  to {
    transform: scale(1.5);
  }
}

