.carousel-slogan {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 100%;
}
.carousel-slogan img {
  max-width: 100%;
  padding-left: 15%;
  padding-right: 15%;
}

.wrap {
  /*Ajuste a largura e altura desejadas aqui*/
  width: 800px;
  height: 300px;

  /*isto fará o elemento video e o .container se adaptarem ao .wrap*/
  position: relative;
}

.wrap > .bg-video {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1; /*apenas um -1 é necessário quando se trabalha com relative + absolute, sendo pai e filho*/
  width: 100%;
  height: 100%;
  overflow: hidden; /* evita do video passar a altura desejada do .wrap */
}
.wrap > .bg-video > video {
  width: 100%;
}